:root{
  --brownColor: rgb(221, 158, 63);
  --grayColor: rgb(71, 71, 71);
  --grayLightColor: rgb(247, 242, 242);
  font-size: 16px;
  font-family: 'Source Serif Pro', serif;
}

body{
  background-color: var(--grayLightColor);
}

h1{
  font-size: 4rem;
}

h2{
  font-size: 3rem;
}
h3{
  font-size: 2rem;
}
h4{
  font-size: 1.3rem;
}

/* Header */

.nav-wrapper{
  background-color: rgba(0,0,0,0.35);
}

header nav{
  color: white;
  overflow: hidden;
  display: flex;
  margin: 0 auto;
  overflow: hidden;
  width: 100%;
  max-width: 1200px;
  align-items: center;
}

header nav img{
  height: 80px;
  margin-left: 10px;
}

header nav ul{
  margin-left: 90px;
  display: flex;
  list-style: none;
}

header nav ul li a{
  font-weight: bold;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  transition: 0.2s;
}

header nav ul li a:hover{
  color: var(--brownColor);
}


header nav ul li i{
  color: var(--brownColor);
  font-size: 5px;
}

header nav div.social-media-box{
  margin-left: auto;
  margin-right: 10px;
  border: 1px solid var(--brownColor);
  padding: 5px 13px;

}

header nav div.social-media-box i{
  transition: 0.3s;
  cursor: pointer;
}

header nav div.social-media-box i:nth-child(2){
  margin: 5px 15px;
}

header nav div.social-media-box i:hover{
  color: var(--brownColor);
}
header li:nth-child(n+2)::before {
  margin: 0 35px;
  content: "•"; 
  color: var(--brownColor)
}
header{
  min-height: 100vh;
  width: 100%;
  background-image: url('./img/background2.jpg');
  background-size: cover;
  background-position: center;
  box-shadow: -3px 0 10px black;
}

.hamb-box{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hamb-box button{
  display: none;
  width: 50px;
  height: 50px;
  background-color: transparent;
  margin-right: 15px;
  border: 2px solid var(--brownColor);
  color: var(--brownColor);
  font-size: 25px;
  outline: none;
}

.navi{
  display: flex;
  align-items: center;
  width: 100%;
}

.navi-wrapper.active{
  display: block;
} 
.navi-wrapper.active .navi{
  flex-direction: column;
}


.navi-wrapper{
  width: 100%;
}

@media (max-width: 900px){
  header nav{
    position: relative;
    flex-direction: column;
    z-index: 5;
    text-align: center;
    background-color: rgba(0,0,0,0.9);
  }

  

  .hamb-box{
    width:100%;
  }

  header nav ul{
    flex-direction: column;
    align-content: center;
    width: 100%;
    margin:0;
  }

  header nav ul li{
    margin: 15px 0;
  }

  header li:nth-child(n+2)::before {
    margin: 0 0;
    content: ""; 
  }

  header nav div.social-media-box{
    margin: 10px auto;
  }

  .hamb-box button{
    display: block;
  }
  .navi-wrapper{
    display:none;
  }


}


/* Banner */

.banner-wrapper{
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  color: rgb(243, 242, 242);
}

.banner-wrapper div:nth-last-of-type(1){
  width: 300px;
  border: 1px solid var(--brownColor);
  margin: 30px auto;
}

.banner-wrapper button{
  background-color: rgba(0,0,0,0.35);
  border: 1px solid var(--brownColor);
  margin-top: 30px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  transition: .3s;
}

.banner-wrapper button a{
  font-family: 'Source Serif Pro', serif;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
  display: block;
  padding: 15px 50px;
}

.banner-wrapper button:hover{
  background-color: var(--brownColor);
}

/* Banner Art */


.banner-art{
  display: flex;
  justify-content: space-between;
  width:100%;
  max-width: 1000px;
  margin: 100px auto;
  color: var(--grayColor);
}

.banner-art div{
  flex-basis: 48%;
}

.banner-art .text-wrapper{
  text-align: center;
  margin: 0 10px;
}

.banner-art .text-wrapper span:nth-of-type(1){
  color: var(--brownColor);
  font-weight: bold;
  text-transform: uppercase;
  display: block;
  margin-bottom: 50px;
}

.banner-art h3{
  margin: 10px 0;
}

.baker-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
}

.baker-wrapper img{
  width: 110px;
  border-radius: 50%;
  padding: 2px;
  border: 2px solid var(--brownColor);
}

.baker-wrapper h4{
  margin: 0 10px;
  border-top: 1px solid var(--grayColor);
  border-bottom: 1px solid var(--grayColor);
  padding: 8px 0;
}

.baker-wrapper p{
  font-size: 0.75rem;
}

.banner-art .text-wrapper span:nth-of-type(2){
  color: var(--brownColor);
  font-weight: bold;
  font-size: 20px;
  text-transform: uppercase;
  display: block;
  margin-top: 25px;
}

.banner-art .images-wrapper{
  border: 14px solid white;
  position: relative;
}

.banner-art .images-wrapper .img-box{
  display: flex;
  flex-wrap: wrap;
  align-items: stretch
}

.banner-art .images-wrapper .img-box div{
  min-width: 50%;
  height: 220px;
}

.banner-art .images-wrapper .img-box div img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.banner-art .img-box-banner{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 200px;
  height: 200px;
  padding: 10px;
  box-shadow: 0 0 15px black;
  background-image: url('./img/marble.jpg')
}
.banner-art .img-box-banner h3{
  box-sizing: border-box;
  display: block;
  border: 1px dashed var(--brownColor);
  text-transform: uppercase;
  width:100%;
  height: 100%;
  margin: 0;
  color: rgb(230, 172, 86);
  padding: 50px 0;
  text-align: center;
}

@media (max-width: 900px){
  .banner-art{
    flex-direction: column;
  }
  .banner-art .text-wrapper{
    margin: 10px 0;
  }
}


/* Open hours banner */
section.opening-hours-banner{
  /* min-height: 100vh; */
  background-image: url('./img/donuts.jpg');
  background-size: cover;
  box-shadow: 0 0 10px black;
  color: var(--grayColor);
  position: relative;
  background-attachment: fixed;
}

.hours-baner-border{
  background-color: var(--grayLightColor);
  min-width:400px;
  width: 30%;
  padding: 15px;
  box-shadow: 0 0 10px black;
  position: relative;
  bottom: 0;
  left: 20%;
  transform: scale(0.9)

}

.hours-banner-wrapper{
  border: 1px dashed rgb(116, 116, 116);
  background-color: rgb(233, 231, 231);
  padding: 20px;
  text-align: center;
}
.hours-banner-wrapper div{
  border: 1px solid var(--brownColor);
  width: 200px;
  margin:0 auto;
}

.hours-banner-wrapper h2{
  margin: 40px 0;
  font-family: 'Dosis', sans-serif;

}

.hours-banner-wrapper h3{
  margin-top: 80px;
  font-family: 'Dosis', sans-serif;
  text-transform: uppercase;
}

.hours-banner-wrapper span{
  font-family: 'Dosis', sans-serif;
  text-transform: uppercase;
}

.hours-banner-wrapper i {
  margin-top: 20px;
  border-radius: 50%;
  font-size: 40px;
  border: 1px solid grey;
  padding: 20px;
}

@media (max-width: 600px){

  .hours-baner-border{
    box-sizing: border-box;
    min-width: 100%;
    position: static;
    margin: 0;
    transform: scale(1);
    
  }
}


/* Footer */

footer {
  background-image: url('./img/marble2.jpg');
  background-size: cover;
}
footer nav{
  max-width: 1200px;
  margin: 0 auto;
}

footer nav ul{
  list-style: none;
  display: flex;
  align-items: center;
}
footer nav ul li {
  flex-grow: 1;
  text-align: center;
  margin: 0 20px;
}
footer nav ul li:nth-child(1),
footer nav ul li:nth-child(2),
footer nav ul li:nth-child(4),
footer nav ul li:nth-child(5){
  border: 1px dashed var(--brownColor);
  background-color: rgba(0,0,0,0.2);
}


footer nav ul a{
  text-decoration: none;
  color: var(--grayLightColor);
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  transition: .3s;
  display: block;
  width: 100%;
  height:100%;
  padding: 5px 0;
}

footer nav ul a:hover{
  color: var(--brownColor);
}

@media (max-width: 900px){
  footer nav ul{
    flex-direction: column;
  }

  footer nav ul a{
    padding: 5px 20px;
    width: 100px;
  }

  footer nav ul li{
    margin: 10px 0;
  }
}

/* Dish */

.dish-wrapper{
  position: relative;
  text-align: center;
  background-color: var(--brownColor);
  border: 2px solid black;
  color: white;
  border-radius: 10px;
  overflow: hidden;
}

.img-dish-wrapper{
  height: 85%;
}

.img-dish-wrapper img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s;
  max-height: 400px;
}

.dish-wrapper button{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: var(--brownColor);
  border: none;
  outline: none;
  opacity: 0;
  transition: 0.3s;
  border-radius: 10px;
}

.dish-wrapper button a{
  border-radius: inherit;
  color: white;
  text-decoration: none;
  font-family: 'Source Serif Pro', serif;
  text-transform: uppercase;
  display: block;
  padding: 10px 40px;
  font-size: 20px;
  border: 1px solid black;
  
}

.dish-wrapper h4{
  line-height: 35px;
}

.price-top{
  position: absolute;
  top:0;
  left: 0;
  color: white;
  font-weight: bold;
  font-size: 20px;
  background-color: var(--brownColor);
  padding: 20px;
  border-radius: 0 0 50% 0;
}


.dish-wrapper:hover img{
  opacity: 0.7;
}
.dish-wrapper:hover button{
  opacity: 1;
}


/*Featured Dishes */
.featured-products{
  margin: 80px 0;
  text-align: center;
}

.featured-products h2{
  color: var(--grayColor);
}

.featured-products > h4{
  color:var(--brownColor);
  text-transform: uppercase;
  margin-bottom: 40px;
}


.featured-dishes{
  max-width: 1000px;
  margin: 0 auto;
  min-height: 50px;
  display: flex;
  justify-content: space-between;
 
}

.select-item{
  max-height: 444px;
  flex-basis: 35%;
  overflow-y: scroll;
}

#scroll::-webkit-scrollbar{

  width: 12px;
  background-color: var(--grayColor);
  border-radius: 10px;
}
#scroll::-webkit-scrollbar-thumb{
  border-radius: 10px;
  background-color: var(--brownColor);
  border: 1px solid black;
}

.select-item div:nth-child(n+2){
  margin-top: 5px;
}

.select-item .dish-wrapper{
  margin-right: 5px;
}

.main-item{
  flex-basis: 63%;
}

@media (max-width: 900px){
  .featured-dishes{
   flex-direction: column;
 }
 .select-item{
   overflow: visible;

   max-height: 9999999px;
 }
 .featured-products .dish-wrapper{
    margin: 5px 5px;
  }
}

/* SingleDish */

.single-dish-wrapper{
  display: flex;
  justify-content: center;
  max-width: 1000px;
  margin: 50px auto;
  min-height: 100px;
  color: var(--grayColor);
  text-align: center;
  overflow: hidden;
}

@media(max-width: 900px){
  .single-dish-wrapper{
   flex-direction: column;
  }
  .single-dish-wrapper div{
    margin: 0 10px;
  }
}

.single-dish-wrapper div{
  flex-basis: 45%;
}

.single-dish-text div:nth-of-type(1){
  width: 200px;
  border: 1px solid var(--brownColor);
  margin: 20px auto;
}

.single-dish-text h4,
.single-dish-text h3{
  text-align: center;
}

.single-dish-text p,
.single-dish-text ul{
  text-align: left;
  list-style: none;
}

.single-dish-text li::before {
  margin: 0 15px;
  content: "•"; 
  color: var(--brownColor)
}

.single-dish-text p{
  margin: 10px 0;
  text-indent: 15px;
}

.single-disc-img{
  transform: rotate(45deg);
  overflow: hidden;
}

.single-disc-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
  transform: rotate(-45deg);
}

.price-wrapper{
  clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
  background-color: var(--brownColor);
  display: inline-block;
  width:100px;
  height: 100px;
  position: relative;
  margin: 10px 0;
}

.price-wrapper h4{
  font-family: 'Dosis', sans-serif;
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.single-dish-text h4 {
  text-align: left;
  margin: 5px 0;
}

/* Menu */

.menu-wrapper{
  max-width: 1100px;
  margin: 50px auto;
}

/* Menu Dishes */
.menu-dishes{
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  min-height: 500px;
  align-content: flex-start;
}

.menu-dishes .dish-wrapper{
  flex-basis: 30%;
  flex-grow: 1;
  min-width: 300px;
  margin: 0 1% 10px 1%;
}

/* MenuForm */

.menu-form{
  display: flex;
  margin: 15px 0;
}

@media(max-width: 900px){
  .menu-form{
   flex-direction: column;
  }

  .input-wrapper{
    margin: 15px 0;
  }
}


.input-wrapper{
  flex-grow: 1;
  text-align: center;
}

.menu-form label{
  font-weight: bold;
  font-size: 25px;
  margin-bottom: 5px;
  color: var(--grayColor);
}

.menu-form{
  background-color: var(--brownColor);
  padding: 10px;
  border: 1px solid black;
  border-radius: 10px;
}

.menu-form select{
  background-color: var(--grayColor);
  color: var(--grayLightColor);
  font-weight: bold;
  text-transform: capitalize;
  width: 100px;
  border: 1px solid black;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
  height:20px;
  font-family: 'Source Serif Pro', serif;
  -webkit-appearance: button;
}


.menu-form input[type='range']{
  width: 200px;
  -webkit-appearance: none;
  outline:none;
  background-color: var(--grayColor);
  height: 5px;
  border-radius: 5px;
}

.menu-form input[type='range']::-webkit-slider-thumb{
  -webkit-appearance: none;
  background: var(--brownColor);
  border: 1px solid black;
  width: 20px;
  height: 20px;
  border-radius: 50%;

}

.menu-form input[type='checkbox'] {
  position: relative;
  background-color: var(--brownColor);
  width: 50px;
  height: 20px;
  border-radius: 20px;
  box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  -webkit-appearance: none;
  outline: none;
  background-color: var(--grayColor);

}

.menu-form .input-wrapper:nth-last-of-type(1) input::before{
  content: '';
  position: absolute;
  width:18px;
  height: 18px;
  border-radius: 50%;
  background-color: var(--brownColor);
  border: 1px solid black;
  top:0;
  left:0;
  transform: scale(1.05);
  transition: 0.3s;
}

.menu-form .input-wrapper:nth-last-of-type(1) input:checked::before{
  left: 30px;
}


/* GALLERY */
.gallery {
  max-width:1200px;
  margin: 50px auto;
}



.gallery h2{
  color: var(--grayColor);
  text-transform: uppercase;
  text-align: center;
}

.gallery .img-box{
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
}


.img-box .img-wrapper{
  flex-basis: 30%;
  flex-grow: 1;
  margin: 3px;
}

.img-box .img-wrapper:hover img{
  transform: scale(1.1);
}

.img-box .img-wrapper:nth-child(4n){
  flex-basis: 80%;
  flex-grow: 1;
}
.img-box .img-wrapper:nth-child(4n):hover img{
  transform: scale(1);
}

.img-box .img-wrapper img{
  width:100%;
  height:100%;
  object-fit: cover;
  transition: 0.3s;
}
@media(max-width: 600px){
  .img-box .img-wrapper{
    flex-basis: 100%;
  }
}

/* Contact */

.contact{
  max-width: 1000px;
  margin: 50px auto;
  display: flex;
  flex-wrap: wrap;
}

@media(max-width: 900px){
  .contact{
    flex-direction: column;
  }
  .contact-text p{
    justify-content: center;
  }
}

.contact h3,
.contact h2{
  color: var(--grayColor);
  text-align: center;
  flex-basis: 100%;
  margin: 15px 0;
}

.contact div{
  flex-basis: 48%;
}

.contact img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.contact p{
  display: flex;
  font-family: 'Source Serif Pro', serif;

}

.contact p *{
  flex-basis: 30%;
}

.contact-text div{
  width: 150px;
  border: 3px solid var(--brownColor);
  margin: 0 auto 40px auto;
  border-radius: 5px;
}